*{
    box-sizing: border-box;
}
/* Nav bar*/
.main{
    width: 100%;
}
.NavBar{
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: #fff;
    display: inline-block;
    padding-block: 10px;
    padding-inline: 3%;
    list-style-type: none;
    text-align: center;
    box-shadow: lightgray 0px 1px 5px 0px;
    position: fixed;
    top:0;
    z-index: 100;
}
.NavBar .a{
    text-decoration: none;
    color: #00307c;
    padding:14px;
    vertical-align: middle;
}
.NavBar li{
    float: left;
}
.NavBar .secret{
    display: none;
}
.NavBar .link.img{
    float: left;
    padding:0;
}
.NavBar .icon{
    vertical-align: middle;
}
.NavBar .iconSecret{
    vertical-align: middle;
    color: #1a1919;
}
.NavBar .link:hover{
    cursor: pointer;
}
.NavBar .link.btn{
    border: 1px solid #d7ad00;
    color: #d7ad00;
    text-align: right;
    float: right;
    padding-block: 10px;
    vertical-align: middle;
}
.NavBar .img{
    height: 40px;
    vertical-align: middle;
}
/*footer */

.Footer{
    width: 100%;
    margin: 0;
    padding: 0;
    background-color: #00307c;
    display: flex;
    padding: 20px;
    text-align: center;
}
.Footer a{
    text-decoration: none;
    color: #fff;
    padding: 14px;
}
.Footer a .icon{
    vertical-align: middle;
}


